.character-preview {
  padding: 10px;
  width: 100px;
  cursor: pointer;

  &:hover {
    background: $clr-light-1;
  }

  img {
    width: 5em;
    height: 5em;
    border-radius: 50%;
    object-position: center;
    object-fit: cover;
    box-shadow: 0 0 1px 0 $clr-dark-1;
  }

  .name {
    font-size: 0.8em;
    text-align: center;
    min-height: 50px;
  }
}
